

import { useNavigate } from 'react-router-dom'
import { createClient } from '@/lib/supabase/client'
import { User, Menu, MessageCircle, Receipt } from 'lucide-react'
import { useEffect, useState } from 'react'
import { supportService } from '@/services/supportService'

interface HeaderProps {
  user: any
  onMenuClick?: () => void
}

const getRoleDisplayName = (role: string) => {
  const roleMap: Record<string, string> = {
    app_admin: ' ',
    ceo: 'CEO',
    final_approver: ' ',
    middle_manager: ' ',
    'lead buyer': ' ',
    buyer: ' ',
    requester: ''
  }
  return roleMap[role] || role
}

export default function Header({ user, onMenuClick }: HeaderProps) {
  const navigate = useNavigate()
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [pendingInquiryCount, setPendingInquiryCount] = useState(0)
  const [pendingStatementCount, setPendingStatementCount] = useState(0)

  const roles = Array.isArray(user?.purchase_role)
    ? user.purchase_role
    : (user?.purchase_role ? [user.purchase_role] : [])
  const isAdmin = roles.includes('app_admin')
  const canSeeStatementBadge = roles.includes('app_admin') || roles.includes('lead buyer')

  // app_admin:     (open+in_progress)  
  useEffect(() => {
    if (!isAdmin) return

    const loadPendingCount = async () => {
      try {
        // count-only   
        const supabase = createClient()
        const { count } = await supabase
          .from('support_inquires')
          .select('id', { count: 'exact', head: true })
          .in('status', ['open', 'in_progress'])

        if (typeof count === 'number') setPendingInquiryCount(count)
      } catch {
        //    UX   
      }
    }

    loadPendingCount()

    const isPending = (status?: string | null) => status === 'open' || status === 'in_progress'
    const subscription = supportService.subscribeToInquiries((payload) => {
      //   (  ): realtime payload  
      const eventType = payload?.eventType as 'INSERT' | 'UPDATE' | 'DELETE' | undefined
      const newRow = payload?.new as { status?: string | null } | undefined
      const oldRow = payload?.old as { status?: string | null } | undefined

      if (eventType === 'INSERT') {
        if (isPending(newRow?.status)) {
          setPendingInquiryCount((prev) => {
            const next = prev + 1
            return next
          })
        }
        return
      }
      if (eventType === 'DELETE') {
        if (isPending(oldRow?.status)) {
          setPendingInquiryCount((prev) => {
            const next = Math.max(0, prev - 1)
            return next
          })
        }
        return
      }
      if (eventType === 'UPDATE') {
        // Supabase realtime payload old.status    (Replica identity / ),
        //        ( UX) +  ( ) .
        if (!oldRow?.status) {
          const isNowPending = isPending(newRow?.status)
          if (!isNowPending) {
            setPendingInquiryCount((prev) => {
              const next = Math.max(0, prev - 1)
              return next
            })
          }
          loadPendingCount()
          return
        }

        const wasPending = isPending(oldRow?.status)
        const isNowPending = isPending(newRow?.status)
        if (wasPending === isNowPending) return
        setPendingInquiryCount((prev) => {
          const next = prev + (isNowPending ? 1 : -1)
          return Math.max(0, next)
        })
      }
    })

    //  /   (  )
    const onVisibilityChange = () => {
      if (!document.hidden) loadPendingCount()
    }
    document.addEventListener('visibilitychange', onVisibilityChange)
    return () => {
      subscription.unsubscribe()
      document.removeEventListener('visibilitychange', onVisibilityChange)
    }
  }, [isAdmin])

  //  :   (open+in_progress)  
  useEffect(() => {
    if (isAdmin) return

    const supabase = createClient()
    let subscription: any
    let cancelled = false
    let currentUserId = ''

    const loadMyPendingCount = async () => {
      if (!currentUserId) {
        const { data: { user: authUser } } = await supabase.auth.getUser()
        currentUserId = authUser?.id || ''
      }
      if (!currentUserId) return

      const { count, error } = await supabase
        .from('support_inquires')
        .select('id', { count: 'exact', head: true })
        .eq('user_id', currentUserId)
        .in('status', ['open', 'in_progress'])

      if (!cancelled) {
        if (!error && typeof count === 'number') setPendingInquiryCount(count)
      }
    }

    loadMyPendingCount()

    //  (     )
    if (!subscription) {
      subscription = supportService.subscribeToInquiries((payload) => {
        if (!currentUserId) {
          loadMyPendingCount()
          return
        }
        const newRow = payload?.new as { user_id?: string } | undefined
        const oldRow = payload?.old as { user_id?: string } | undefined
        if (newRow?.user_id !== currentUserId && oldRow?.user_id !== currentUserId) return
        loadMyPendingCount()
      })
    }

    return () => {
      cancelled = true
      if (subscription) subscription.unsubscribe()
    }
  }, [isAdmin])

  useEffect(() => {
    if (!canSeeStatementBadge) return
    const supabase = createClient()
    let cancelled = false
    let subscription: any

    const loadPendingStatements = async () => {
      const { count } = await supabase
        .from('transaction_statements')
        .select('id', { count: 'exact', head: true })
        .in('status', ['pending', 'processing', 'extracted'])

      if (!cancelled && typeof count === 'number') {
        setPendingStatementCount(count)
      }
    }

    loadPendingStatements()

    subscription = supabase
      .channel('transaction-statements-header-badge')
      .on(
        'postgres_changes',
        {
          event: '*',
          schema: 'public',
          table: 'transaction_statements'
        },
        () => {
          loadPendingStatements()
        }
      )
      .subscribe()

    return () => {
      cancelled = true
      if (subscription) supabase.removeChannel(subscription)
    }
  }, [canSeeStatementBadge])
  
  const handleLogout = async () => {
    const supabase = createClient()
    await supabase.auth.signOut()
    navigate('/login')
  }

  return (
    <header className="bg-white border-b border-gray-200 fixed top-0 left-0 right-0 z-50">
      <div className="h-14 px-4 sm:px-6 flex items-center justify-between">
        {/*    */}
        <button
          onClick={onMenuClick}
          className="lg:hidden p-2 rounded hover:bg-gray-50 transition-colors"
        >
          <Menu className="w-5 h-5 text-gray-600" />
        </button>

        {/*  */}
        <div className="flex items-center">
          <img
            src="/logo_symbol.svg"
            alt="HANSL Logo"
            className="w-14 h-14"
            style={{ objectFit: 'contain' }}
          />
          <div className="ml-3 leading-none flex flex-col items-start">
            <h1 className="text-[30px] font-bold text-gray-600 leading-none mb-0.5">
              HANSL
            </h1>
            <span className="text-[11px] font-medium text-gray-500 uppercase tracking-wide leading-none ml-[1px]">
              Purchase System
            </span>
          </div>

          {/*     */}
          {pendingInquiryCount > 0 && (
            <button
              type="button"
              onClick={() => navigate('/support')}
              className="relative ml-3 inline-flex items-center justify-center w-9 h-9 rounded-lg hover:bg-gray-50 transition-colors"
              title="  "
              aria-label={`  ${pendingInquiryCount}`}
            >
              <MessageCircle className="w-4 h-4 text-gray-600" />
              <span className="absolute -top-1 -right-1 min-w-[18px] h-[18px] flex items-center justify-center text-[10px] font-bold text-white bg-red-500 rounded-full px-1">
                {(pendingInquiryCount > 99) ? '99+' : pendingInquiryCount}
              </span>
            </button>
          )}
          {canSeeStatementBadge && pendingStatementCount > 0 && (
            <button
              type="button"
              onClick={() => navigate('/transaction-statement')}
              className="relative ml-2 inline-flex items-center justify-center w-9 h-9 rounded-lg hover:bg-gray-50 transition-colors"
              title="  "
              aria-label={`  ${pendingStatementCount}`}
            >
              <Receipt className="w-4 h-4 text-gray-600" />
              <span className="absolute -top-1 -right-1 min-w-[18px] h-[18px] flex items-center justify-center text-[10px] font-bold text-white bg-red-500 rounded-full px-1">
                {(pendingStatementCount > 99) ? '99+' : pendingStatementCount}
              </span>
            </button>
          )}
        </div>
        
        {/*   */}
        <div className="hidden sm:flex items-center gap-4">
          <div className="flex items-center gap-2">
            <span className="text-sm font-medium text-gray-700">
              {user?.name || ''}
            </span>
            {user?.purchase_role && (
              <span className="text-xs text-gray-500">
                {getRoleDisplayName(user.purchase_role)}
              </span>
            )}
          </div>
          <button
            onClick={handleLogout}
            className="px-3 py-1.5 text-xs font-medium text-gray-500 hover:text-gray-700 transition-colors"
          >
            
          </button>
        </div>

        {/*    */}
        <div className="sm:hidden">
          <button
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
            className="p-2 rounded hover:bg-gray-50 transition-colors"
          >
            <User className="w-5 h-5 text-gray-600" />
          </button>
        </div>
      </div>
    </header>
  )
}